@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #2c2c2c;
  background: #f9f9f9;
  padding: 5px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

fieldset {
  border: none;
}

.visually-hidden {
  visibility: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
}

@media screen and (min-width: 320px) {
  body {
    padding: 10px;
  }

  .container {
    grid-gap: 15px;
  }
}
@media screen and (min-width: 720px) {
  body {
    padding: 20px;
  }

  .container {
    grid-gap: 20px;
    grid-template-columns: 1fr 2fr;
  }
}
@media screen and (min-width: 1200px) {
  body {
    padding: 28px;
  }

  .container {
    grid-gap: 28px;
    grid-template-columns: 1fr 3fr;
  }
}
@media screen and (min-width: 1200px) {
  .header {
    margin-bottom: 20px;
  }
}
.header__logo-link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header__image {
  width: 65px;
}
.header__title {
  font-weight: 600;
  font-size: 32px;
  color: #7e72f2;
}
@media screen and (min-width: 720px) {
  .header__title {
    font-size: 48px;
  }
}

.breadcrumbs {
  margin-top: 15px;
}
@media screen and (min-width: 720px) {
  .breadcrumbs {
    margin-top: 20px;
    grid-column: 1/3;
  }
}
.breadcrumbs__item {
  display: inline-block;
}
.breadcrumbs__item::after {
  content: ">>";
  display: inline-block;
  width: 25px;
  text-align: center;
}
@media screen and (min-width: 720px) {
  .breadcrumbs__item::after {
    width: 40px;
  }
}
.breadcrumbs__item:last-child::after {
  content: "";
  display: none;
}
.breadcrumbs__link {
  color: #7e72f2;
}

.filter-form {
  display: none;
}
@media screen and (min-width: 720px) {
  .filter-form {
    display: flex;
    flex-direction: column;
    max-width: 375px;
  }
  .filter-form__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .filter-form__submit-button {
    width: 40px;
    height: 40px;
    background: #fff;
    border: none;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  }
  .filter-form__list-wrapper {
    flex-grow: 1;
    max-width: 560px;
    background: #fff;
    margin-top: 15px;
    margin-bottom: 15px;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  }
}
@media screen and (min-width: 720px) and (min-width: 1200px) {
  .filter-form__list-wrapper {
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 720px) {
  .filter-form__clear-button {
    width: 100%;
    padding: 17px;
    text-transform: uppercase;
    color: #fff;
    background: #6f64f8;
    border: none;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  }
}

.filters {
  width: 95%;
}
.filters__group {
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 28px;
}
.filters input {
  opacity: 0;
}
.filters label:hover,
.filters input:hover {
  cursor: pointer;
}
.filters hr {
  margin: 28px 0;
}
.filters__option_radio label::before {
  border-radius: 50%;
}
.filters__option_checkbox {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.filters__option_checkbox label::before {
  border-radius: 3px;
}
.filters__option label::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-left: -15px;
  margin-right: 10px;
  border: 1px solid #979797;
}
.filters__option input:checked + label::before {
  background: #6f64f8;
}

.catalog {
  display: grid;
  grid-auto-rows: 486px;
  grid-gap: 15px;
}
@media screen and (min-width: 320px) {
  .catalog {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}
@media screen and (min-width: 720px) {
  .catalog {
    grid-gap: 20px;
  }
}
@media screen and (min-width: 1200px) {
  .catalog {
    grid-gap: 28px;
    grid-template-columns: repeat(3, 1fr);
  }
}

.search {
  position: relative;
  margin-bottom: 15px;
}
@media screen and (min-width: 1200px) {
  .search {
    margin-bottom: 20px;
  }
}
.search__results {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.search__button-like {
  width: 45px;
  height: 40px;
  border-radius: 8px;
  border: none;
  background: #6f64f8;
  align-self: baseline;
}
.search__input {
  width: 100%;
  margin-top: 15px;
  padding: 15px 25px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border: none;
  border-radius: 8px;
  font-size: 18px;
}
@media screen and (min-width: 1200px) {
  .search__input {
    margin-top: 20px;
  }
}
.search__image {
  position: absolute;
  right: 15px;
  bottom: 15px;
}

.card {
  display: grid;
  align-items: end;
  width: 100%;
  min-width: 300px;
  font-weight: 400;
  font-size: 14px;
  background: #fff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: 8px;
}
.card__image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 337px;
}
.card__image img {
  max-width: 80%;
}
.card__body {
  padding: 0 24px;
}
.card__body h4 {
  margin-top: 16px;
  font-weight: 400;
  font-size: 16px;
}
.card__body p {
  height: 30px;
  clear: both;
  font-size: 12px;
}
.card__rate {
  float: left;
  display: flex;
  justify-content: space-around;
  width: 47px;
  padding: 6px 9px;
  font-weight: 400;
  font-size: 14px;
  background: #6f64f8;
  color: #fff;
  border-radius: 3px;
}
.card__price {
  float: right;
  font-weight: 400;
  font-size: 14px;
}
.card__footer {
  display: flex;
  height: 45px;
}
.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  border: none;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
}
.card__button_wishlist {
  background: #ededed;
  border-bottom-left-radius: 8px;
}
.card__button_wishlist img {
  margin-right: 9px;
}
.card__button_cart {
  background: #6f64f8;
  color: #fff;
  border-bottom-right-radius: 8px;
}
.card__button_cart img {
  margin-right: 12px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 10px 0;
}
.pagination__list {
  display: flex;
  justify-content: center;
  width: fit-content;
  margin: 0 10px;
  background: #ededed;
  border-radius: 7px;
}
.pagination__link_arrow, .pagination__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  font-size: 12px;
  background: #ededed;
  border-radius: 50%;
}
.pagination__item_current {
  border-radius: 50%;
  color: #fff;
  background: #6f64f8;
}
@media screen and (min-width: 720px) {
  .pagination {
    grid-column: 1/3;
  }
  .pagination__list {
    border-radius: 15px;
  }
  .pagination__link_arrow, .pagination__item {
    width: 30px;
    height: 30px;
    font-size: 16px;
  }
}
@media screen and (min-width: 1200px) {
  .pagination__list {
    margin: 0 17px;
    border-radius: 20px;
  }
  .pagination__link_arrow, .pagination__item {
    width: 40px;
    height: 40px;
    font-size: 18px;
  }
}